.footer{
    width: 100%;
    height: 500px;
    border: 1px solid red;
    display: flex;
    flex-direction: row;
}
.footer-top{
    width: 90%;
    height: 500px;
    border: 1px solid red;
    margin: auto;
    display: flex;
    flex-direction: row;
}
.footer-left{
    display: flex;
    
    flex-direction: row;
    
    
}
.footer-left-contact{
    display: flex;
    flex-direction: column;
    width: 330px;
    height: 400px;
    /* border: 1px solid red; */
    margin-right: 25px;
}

.footer-left-heading{
    font-size:36px;
    font-weight: 800;
    color: #0f141e;
    text-align: left;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
    margin-bottom: 15px;
}
.footer-left-para{
    font-size: 20px;
    font-weight: 500;
    color: #878a8f;
    text-align: left;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
}

.footer-left-bottom{
    display: flex;
    flex-direction: column;
    text-align: start;
    list-style: none;
    
}
.footer-left-bottomlist{
    text-align: left;
    color: #878a8f;
    font-size: 18px;
    font-family: "Playfair Display";
}
.footer-left-mail{
    margin-left: 5px;
    color:#0f141e ;
    text-decoration: none;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";

}


/* ----------footer center------------- */

.footer-center{
    display: flex;
    flex-direction: column;
    width: 260px;
    height: 400px;
    /* border: 1px solid red; */
    margin-left: 70px;
    margin-right: 39px;
}
.footer-center-heading{
    padding-left: 36px;
    font-size:36px;
    font-weight: 800;
    color: #0f141e;
    text-align: left;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
    margin-bottom: 15px;
}
.footer-center-ul{
    text-align: left;
}
.footer-center-para{
    font-size: 18px;
    font-weight: 500;
    color: #878a8f;
    text-align: left;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
}

.footer-right{
    display: flex;
    flex-direction: column;
    width: 230px;
    height: 400px;
    /* border: 1px solid red; */
    margin-left: 70px;
}

.footer-right-heading{
    font-size:36px;
    font-weight: 800;
    color: #0f141e;
    text-align: left;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
    margin-bottom: 15px;
    padding-left: 39px;
}

.footer-right-ul{
    list-style: none;
    text-align: start;
}

.footer-right-arr{
    line-height: 1.5625rem;
    text-decoration: none;
    color: #0f141e;
    font-size: 20px;
    font-weight: 500;
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
}