.header{
    display: flex;
    margin-bottom: 45px;
    
}
.header-left{
    display: flex;
    
    flex-direction: column;
    margin-top: 40px;
    margin-left: 40px;
    color: #0f141e;
    text-decoration: none;
    
    
}
.header-headlineOne{
    /* font-family: "Helvetica Now Display DW",sans-serif; */
    font-family: "Playfair Display";
    
    font-size: 25px;
    font-weight: 700;
    letter-spacing: -.01em;
    text-align: left;
}
.header-heading{
    /* font-family: "Helvetica Now Display DW",sans-serif; */

    font-family: "Playfair Display";
    font-size: 13px;
    color: #38383a;
    font-weight: 600;
    text-align: left;
    letter-spacing: 3px;
    
}
.header-right{
    display: flex;
    flex-direction: row;
    position: absolute;
    color: #0f141e;
    
    right: 0;
    margin-top: 40px;
    margin-right: 40px;
    text-decoration: none;
    
    font-family: "Helvetica Now Display DW",sans-serif;
    font-style: normal;
    z-index: 1;
}
.header-menu-nav{
    font-size: 18px;
    display: block;
    
    line-height: 1.0625rem;
    font-weight: 700;

    padding-top: 12px;    
}


/* -------------menu--------------- */

.menu{
    margin-left: 7px;
    width: 40px;
    height: 40px;
    /* border: 1px solid red; */
    position: relative;
    
}

.line-one{
    width: 70%;
    height: 3px;
    background-color: #0f141e;
    border-radius: 5px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.3s, width 0.5s;
}
.line-two{
    width: 70%;
    height: 3px;
    background-color: #0f141e;
    border-radius: 5px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -10px);
    transition: transform 0.3s, width 0.5s;
}
.line-three{
    width: 70%;
    height: 3px;
    background-color: #0f141e;
    border-radius: 5px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 8px);
    transition: transform 0.3s, width 0.5s;

}


.openmenu .line-one{
    
    width: 0;
}
.openmenu .line-three{
    transform: translate(-50%, -50%) rotate(-45deg);
    
}
.openmenu .line-two{
    
    transform: translate(-50%, -50%) rotate(45deg);
}



/* -----------menu box---------- */

.open-box-span{
    color: rgb(165, 172, 179);
    font-weight: 600;
    
}

.menu-box{
    width: 0;
    height: 0;
    background: rgba(0,0,0,0.8);
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    
    
    
    border-bottom-left-radius:100% ;
    transition: width 0.5s, height 0.5s, border-redius 0.5s;
}

.menu-box.openbox{
    width: 100%;
    height: 100vh;
    border-bottom-left-radius:0 ;
    
}
.menu-box.openbox ul li{
    opacity: 1;
    transform: translateY(0);
    transition: transform 1s, opacity 1s;
    transition-delay: 0.5s;
}


.menu-box ul li{
    list-style: none;
    margin: 7px;
    text-align: center;

    opacity: 0;
    transform: translateY(30px);
}
.menu-box ul li a{
    display: inline-block;
    text-decoration: none;
    color: #fff;
    font-size: 40px;
    font-family: "Helvetica Now Display DW",sans-serif;
    font-weight: 600;
    position: relative;
    text-shadow: 0 0 0 red;
    transition: text-shadow 0.5s, transform 0.5s;
}

.menu-box ul li a:hover{
    text-shadow: -3px 3px 0 red;
    transform:  translateY(-6px);
}

.menu-box ul li a::after{
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #fff;
    position: absolute;
    bottom: 12px;
    right: -18px;
    opacity: 0;
    box-shadow: 0 0 0 red;
    transition: box-shadow 0.5s;
}

.menu-box ul li a:hover::after{
    opacity: 1;
    box-shadow: -2px 2px 0 red;
}
